
.box-image {

}

.Images {
    display: flex;
    justify-content: space-evenly;
    min-width: 100%;
    min-height: 100%; 
    padding: 12px;
    

}

.sub-image {
    margin:  0 30px;
}
.sub-image ul {
    display: flex;
    text-decoration: none;
}

.Images img {
    width: 80px;
    margin: 0 30px ;
}

.sub-image ul li  {
    text-decoration: none;
    list-style: none;
    text-align: center;
    cursor: pointer;
    transition: ease-out;
}

.sub-image ul li a {
    text-decoration: none;
}

.sub-image ul :hover {
    color: blue;
    scale: 1.1;
}

