* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    font-family: Arial, Helvetica, sans-serif;
}

.flip-navbar img {
    width: 81px;
    margin-left: 180px;
}

.flip-navbar {
    background-color: #2874f0;
    display: flex;
    align-items: center;
    justify-content: space-around;
    height: 56px;
    width: 100%;
}

.flip-navbar input {
   text-align: center;
   width: 400px;
   height: 30px;
   margin-right: 479px;
   border-radius: 2px;
   border-style: none;
   
}

.listItem {
    
}

.listItem ul {
    display: flex;
    text-decoration: none;
}

.listItem ul li{
    list-style: none;
    margin-right:20px;
    
}

.listItem ul li a {
    color: #fff;    
    text-decoration: none;
    font-weight: bold;
}

.listItem ul li #login{
    background-color: #ada6a6;
    color: #2874f0;
    padding: 6px 8px;
}
