.flipkart-Login-section {
    background-color: #fff;
    font-family: Arial, Helvetica, sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    box-shadow: 6.5;
}

.flipKart-logo {
    display: flex;
    background-color:#2874f0;
    width: 350px;
}

.flipKart-logo img {   
    padding:114px;
    width: 300px;
}

.Login-section{
    background-color:aqua;
    display: flex;
}

.flipkart-form-section {
    background-color:cadetblue;
    height: 50vh;
    display: flex;
    align-items: center;
}

.form-section{
    background-color:aliceblue;
    text-align: center;
    height: 100%;
    padding-top: 30px;
}

.form-section p {
    color: grey;
}

.form-section span {
    color: #2874f0;
    cursor: pointer;
}

.form-section h2 {
    font-weight: bold;
    font-size: 30px;
}

.form-section h5 {
    color: gray;
}

.login {
    margin-top: 30px;
}

.login-password {
    margin-top: 30px;
}

.Login-signup {
    margin-top: 20px;
}

.Login-signup #login {
    background-color:#2874f0;
    color: #fff;
    margin: 10px;
    width: 110px;
    height: 30px;
    border-radius: 15px;
    font-size: 16px;
    font-weight: bold;
    cursor: pointer;
}

.Login-signup #signup {
    background-color: #fff;
    color:#2874f0;
    margin: 10px;
    width: 110px;
    height: 30px;
    border-radius: 15px;
    border-color: blue;
    font-size: 16px;
    font-weight: bold;
    cursor: pointer;
}

.form-section p {
    margin-top: 30px;
}

.form-section h1 {

}

.login-email input {
    font-size: 16px;
    height: 30px;
    border-radius: 15px;
    padding: 8px;
    border-color: grey;
    border-style: none;
    margin-top: 5px;
}

.login-password h6 {
    color: #2874f0;
}

.login-password input {
    font-size: 16px;
    height: 30px;
    border-radius: 15px;
    padding: 8px;
    border-color: grey;
    border-style: none;
}